import React from "react" 
import Container from "./Container"
import { useStaticQuery, graphql } from "gatsby" 
import StyledBackgroundSection from './BackgroundSection' 
import ClockLive from "./helpers/Clock"
import WaveHeader from "./helpers/WaveHeader"
import { Link } from "gatsby"
import { ArrowRightShort } from 'react-bootstrap-icons';

export default function Header({ children }) {
  
  const data = useStaticQuery(graphql`
      query HeaderQuery {
        site {
          siteMetadata {
            title,
            description
          }
        }
      }
    `)

  return (
    <div className="header">
      <WaveHeader fill="#fb9d9d" />  
      <StyledBackgroundSection className="d-flex align-items-center">
      <Container> 
      <div className="row align-items-center">
        <div className="col-md-7 col-12">
          <div  className="restaurant"> 
            <div className="title">
            <h2>Restaurante Arequipeo</h2>
            <h1 class="glitch" data-text={data.site.siteMetadata.title}>{data.site.siteMetadata.title}</h1>
            </div>
            <p className="lead">{data.site.siteMetadata.description}</p>
            <Link to="/cliente" className="btn btn-lg btn-primary">Realizar Pedido <ArrowRightShort /></Link> 
            <Link to="/sugerencias" className="ms-md-3 mt-2 mt-md-0 btn btn-lg   btn-secondary"
            >Deja tus sugerencias <ArrowRightShort /></Link> 
          </div>
        </div>
        <div className="col-md-5 col-12">
          <div className="large-time text-center mt-3 mt-md-0">
          <div className="h1 d-inline-block ">
          <ClockLive format="HH:mm:ss"/>
          
          </div>
          <h3><ClockLive format="[Hoy es] dddd"/></h3>
          </div>
        </div>
      </div>
      </Container> 
      </StyledBackgroundSection>  
      
    </div>
  )
}