import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from "gatsby-plugin-image" //GatsbyImage
import styled from '@emotion/styled';
import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from 'gatsby-background-image'
const GbiBridged = (props) => {
  const { placeholderImage } = useStaticQuery(
    graphql`
      query {
        placeholderImage: file(relativePath: { eq: "paste.png" }) {
          childImageSharp {
            gatsbyImageData(
              width: 1000
              placeholder: BLURRED
              formats: [AUTO, WEBP, AVIF]
            )
          }
        }
      }
    `
  )
  const image = getImage(placeholderImage)

  // Use like this:
  //backgroundColor={`#040e18`}
  const bgImage = convertToBgImage(image) 
  const PageThumbWrapper = styled(BackgroundImage)` 
    position: relative;
    width: 100%;
    height: 530px;
    @media screen and (max-width: 650px) {
      height: 300px;
    }
  `;
  console.log(props);
  return (
    
    <PageThumbWrapper className={props.className}  {...bgImage}  preserveStackingContext> 
      {props.children}
    </PageThumbWrapper>
  )
}
export default GbiBridged