import React, { useEffect, useState } from 'react';
import Container from "./Container"
//import Speedometer from "./helpers/Speedometer" frecuente
  
//<Speedometer /> 
import { Link } from "gatsby"
import { Mailbox  } from 'react-bootstrap-icons';
import Reloj from './helpers/AnalogClock';
//import 'react-clock/dist/Clock.css';

export default function Home({ children }) {
  const [value, setValue] = useState(new Date());
  useEffect(() => {
    const interval = setInterval(
      () => setValue(new Date()),
      1000
    );

    return () => {
      clearInterval(interval);
    }
  }, []);
  return (
    <div className="home py-5">
    <Container>  
      
    <Reloj /> 
      <div className="text-center py-5">
         <h2>Bienvenido</h2>
          <p>Solicita tu menu a travs de las siguientes opciones</p>
          <Link to="/cliente/nuevo"   className="btn btn-success m-1">Inscribirse</Link>
          <Link to="/cliente"   className="btn btn-primary m-1">Pedidos Aqui</Link> 
      </div>  
      <div class="row"> 
        <div class="col-12 col-md-7 offset-md-5 text-end py-5">
              
                <h2>Deja tus sugerencias</h2> 
                <p>Nos adaptamos a tus necesidades de alimentacin, puedes sugerirnos desde platillos nuevos hasta nuestra forma de atencon, Todo ser tomado en cuenta para mejorar tu estada en nuestra cadena de restaurantes</p>
                <Link to="/sugerencias"  className="btn btn-lg btn-secondary m-1">Haz tus Sugerencias Aqui <Mailbox size={24} style={{
                  marginBottom: "3px",
                }}/></Link> 
          
          </div>
        </div> 
    </Container>
    </div>
  )
}